import React , { useState , useEffect } from "react";
import { Input, Modal , DatePicker } from "antd";
import { SaveFilled } from "@ant-design/icons";
import IntlMessages from "util/IntlMessages";
import { FormattedMessage } from "react-intl";
import moment from "moment";

const RangePicker = DatePicker.RangePicker;
const {TextArea} = Input;


const EditCoupon = ({ onSaveCoupon, onToggleModal, open, coupon }) => {
  
    const [name, setName] = useState('')
    const [code, setCode] = useState('')
    const [discount, setDiscount] = useState('')
    const [description, setDescription] = useState('')
    const [start, setStartDate] = useState('')
    const [end, setEndDate] = useState('')

    useEffect(() => {
      if(Object.keys(coupon).length !== 0){
        console.log(coupon.name)
        setName(coupon.name)
        setCode(coupon.code)
        setDiscount(coupon.discount)
        setDescription(coupon.description)
        setStartDate(coupon.start)
        setEndDate(coupon.end)
      }
    }, [coupon])

    const onChange = (dates, dateStrings) => {
      setStartDate(dateStrings[0])
      setEndDate(dateStrings[1])
      console.log(dates);
    }

    return (
      <Modal
        okText={<SaveFilled />}
        cancelText={ <IntlMessages id="modal.cancelSave"/> }
        title={<IntlMessages id="coupon.saveCoupon"/>}
        toggle={onToggleModal} 
        visible={open}
        closable={false}
        onOk={() => {
          if (name === '')
            return;
          onToggleModal("editCouponState");
          onSaveCoupon({ id:coupon.id , name , code , discount , description , start , end });
          setName(name)
          setName(name)
          setCode(code)
          setDiscount(discount)
          setDescription(description)
          setStartDate(start)
          setEndDate(end)
        }}
        onCancel={()=> {
          onToggleModal('editCouponState')
          setName(coupon.name)
          setCode(coupon.code)
          setDiscount(coupon.discount)
          setDescription(coupon.description)
          setStartDate(coupon.start)
          setEndDate(coupon.end)
        }}>

        <div  className="gx-modal-box-row">
          <div className="gx-modal-box-form-item">
          <div className="gx-form-group">
            <FormattedMessage id="columns.name" defaultMessage="name">
             {
               placeholder => (
                <Input
                required
                value={name}
                placeholder={placeholder}
                onChange={(event) => setName(event.target.value)}
                margin="none"/>
                )
              }
           </FormattedMessage>
            </div>
            <div className="gx-form-group">
            <FormattedMessage id="columns.code" defaultMessage="code">
             {
               placeholder => (
                <Input
                required
                value={code}
                placeholder={placeholder}
                onChange={(event) => setCode(event.target.value)}
                margin="none"/>
                )
              }
           </FormattedMessage>
            </div>

            <div className="gx-form-group">
            <FormattedMessage id="columns.discount" defaultMessage="discount">
             {
               placeholder => (
                <Input
                required
                value={discount}
                placeholder={placeholder}
                onChange={(event) => setDiscount(event.target.value)}
                margin="none"/>
                )
              }
           </FormattedMessage>
            </div>

            <div className="gx-form-group">
              <FormattedMessage id="columns.description" defaultMessage="description">
              {
                placeholder => (
                  <TextArea
                  required
                  value={description}
                  placeholder={placeholder}
                  onChange={(event) =>   setDescription(event.target.value)}
                  margin="none"/>
                  )
                }
            </FormattedMessage>
            </div>

            <div className="gx-form-group">
              <RangePicker value={[moment(start),moment(end)]} className="gx-mb-3 gx-w-100" ranges={{Today: [moment(), moment()], 'This Month': [moment(), moment().endOf('month')]}} onChange={onChange} />
            </div>
          </div>
        </div>
      </Modal>
    );
}

export default React.memo(EditCoupon);
